
.item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 24px 0px;
}
.item__description-block {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.item__text {
    padding-right: 32px;
}
.item__description {
    margin: 2px;
    font-size: 18px;
}
.item__title {
    margin: 4px 0px;
    font-size: 32px;
}
.item__image-wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    padding-right: 16px;
}
.item__actions {
    padding-left: 16px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.item__actions button {
      width: auto;
}



