
.shop-top-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 48px;
}
.shop-top-bar .icon {
    height: 32px;
    max-width: 48px;
}
.shop-top-bar .shop-icon, .shop-top-bar .actions {
    padding: 8px 8px;
}
.shop-top-bar .title {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    text-align: center;
    line-height: 48px;
    font-size: 32px;
    text-transform: uppercase;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.shop-top-bar .title__icon {
      margin-right: 16px;
}



