
@import url("https://fonts.googleapis.com/css?family=Open+Sans");
form.big input, form.big textarea {
  font-size: 150%;
  padding: 8px;
}
form.big button {
  font-size: 150%;
}
form.big.vertical input, form.big.vertical textarea {
  margin: 16px 0px;
}
button, textarea, input {
  border: 0px solid #000;
}
button:disabled, textarea:disabled, input:disabled {
    cursor: not-allowed;
}
input, textarea {
  background: linear-gradient(70deg, rgba(170, 170, 170, 0.5), rgba(153, 153, 153, 0.5));
  color: #333333;
  caret-color: blue;
  resize: none;
  padding: 8px;
}
textarea {
  padding: 8px;
}
input {
  padding: 4px;
}
body {
  font-family: 'Open Sans', 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 15px;
  overflow-y: auto;
}
* {
  box-sizing: border-box;
}
.full-width {
  width: 100%;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
a:not(.link--discrete) {
  color: #222222;
  font-style: italic;
  text-decoration: none;
  font-weight: bold;
}
a:not(.link--discrete):hover {
    color: #555555;
}
a:not(.link--discrete):active {
    color: #888888;
}
button:hover {
  cursor: pointer;
}
.btn {
  padding: 0px;
  margin: 0px;
  background-color: transparent;
  transition: background-color 0.3s, color 0.3s, -webkit-transform 0.3s;
  transition: background-color 0.3s, color 0.3s, transform 0.3s;
  transition: background-color 0.3s, color 0.3s, transform 0.3s, -webkit-transform 0.3s;
}
.btn:hover {
    cursor: pointer;
}
.btn:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
}
.btn:disabled {
    cursor: not-allowed;
}
.btn--zooming:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
ul.horizontal {
  list-style-type: none;
}
ul.horizontal li {
    display: inline;
    margin: 4px;
}
.list--no-style {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}
div::-webkit-scrollbar-track, main::-webkit-scrollbar-track, aside::-webkit-scrollbar-track, nav::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: rgba(245, 245, 245, 0.5);
}
div::-webkit-scrollbar, main::-webkit-scrollbar, aside::-webkit-scrollbar, nav::-webkit-scrollbar {
  width: 14px;
}
div::-webkit-scrollbar-thumb, main::-webkit-scrollbar-thumb, aside::-webkit-scrollbar-thumb, nav::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.3);
  background: linear-gradient(70deg, rgba(50, 130, 184, 0.5), rgba(76, 149, 176, 0.5));
}
