
.flex :first-child {
  margin-right: 8px;
}
.flex :not(:first-child) {
  margin-left: 8px;
}
.flex .input-100 {
  width: 100%;
  margin-right: inherit;
}
.flex .input-100 input {
    width: 100%;
}
.flex input[type=checkbox] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
}
.flex input[type=checkbox] {
  -border-radius: 4px;
  height: 30px;
  width: 30px;
  background: linear-gradient(70deg, rgba(170, 170, 170, 0.5), rgba(153, 153, 153, 0.5));
  border: 1px solid #ccc;
}
.flex input[type="checkbox"]:checked {
  background: linear-gradient(70deg, rgba(170, 170, 170, 0.5), rgba(153, 153, 153, 0.5));
  /*margin:0px;*/
  position: relative;
}
.flex input[type="checkbox"]:checked:before {
    font-family: FontAwesome;
    content: '\f00c';
    display: block;
    color: grey;
    font-size: 13px;
    position: absolute;
}
.flex label {
  position: relative;
  bottom: 28px;
}
.flex2 {
  margin-top: 65px;
}



