
#left-menu {
  position: fixed;
  left: 0;
  height: 100vh;
  min-height: 500px;
  min-width: 250px;
  background: linear-gradient(90deg, rgba(34, 34, 34, 0.7), rgba(51, 51, 51, 0.5));
  padding-top: 48px;
  padding-bottom: 16px;
}
#left-menu .menu-item {
    font-size: 120%;
    border-image-source: linear-gradient(90deg, transparent 10%, rgba(255, 255, 255, 0.2), transparent 90%);
    border-image-slice: 100% 0%;
    border-image-width: 0px 0px 2px 0px;
    border-style: solid;
}
#left-menu button {
    color: #ffffff;
    width: 100%;
    padding: 16px 32px;
    background-color: transparent;
}
#left-menu button:hover {
      background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.2), transparent);
}
#left-menu .menu-item:last-of-type {
  color: red;
}



