
.altai-button {
  border-radius: 5%;
  background-color: #38393B;
  color: #ffffff;
  padding: 12px 24px;
  text-transform: uppercase;
  transition: background-color 0.3s, color 0.3s;
}
.altai-button:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.altai-button:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
}
.altai-button:disabled {
    background-color: #989aa1;
    color: #6f7175;
    cursor: not-allowed;
}
.altai-button--round {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    padding: 0px;
    text-align: center;
}
.altai-button--flat {
    padding: 0px;
    background-color: transparent;
    height: auto;
}
.altai-button--flat:disabled {
      background-color: transparent;
}



