
.message {
  padding: 8px 24px;
}
.message .bubble {
    border-radius: 5px;
    width: 60%;
    margin: 4px 0px;
    min-width: 100px;
    padding: 8px 24px 12px 24px;
}
.message .info-bar {
    font-size: 80%;
    padding-bottom: 8px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.message .info-bar .sender {
      font-weight: bold;
}
.message.default .bubble {
    margin-left: auto;
    background: linear-gradient(70deg, rgba(153, 153, 153, 0.5), rgba(136, 136, 136, 0.5));
}
.message.self .bubble {
    margin-right: auto;
    color: #ffffff;
    background: linear-gradient(70deg, rgba(50, 130, 184, 0.5), rgba(76, 149, 176, 0.5));
}



